.button {
  padding: 7px 44px;
  cursor: pointer;
  border: 0;
  background-color: $basic_green;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

a.button--intro {
  display: block;
  width: 100%;
  color: $basic_white;

  @media (min-width: $tablet-width) {
    position: relative;
    top: -600px;
    left: 0;
    width: 244px;
    height: 54px;
    margin-left: 112px;
    padding: 14px;
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    top: -500px;
  }

}

.main-nav__toogle {
  background: transparent;
  border: none;
}

.page-header__wrapper button.main-nav__toogle {
  position: relative;
  width: 24px;
  height: 16px;
  cursor: pointer;
}

.main-nav--closed::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1.5px;
  background-color: $basic_color;
  box-shadow: 0 -8px 0 0 $basic_color, 0 8px 0 0 $basic_color;
}

.main-nav--opened::before,
.main-nav--opened::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 25px;
    height: 1.5px;
    transform: rotate(45deg);
    background-color: $basic_color;
}

.main-nav--opened::after {
    transform: rotate(-45deg);
}

.form__button {
  width: 100%;
  color: $basic_white;

  @media (min-width: $tablet-width) {
    width: 50%;
    max-width: 282px;
    max-height: 52px;
  }

  @media (min-width: $desktop-width) {
    max-width: 568px;
    margin-right: 81px;
  }
}

.program-select__need {
  text-align: center;
  margin-bottom: 32px;
}

a.button--product {
  width: 100%;
  padding-top: 10px;
  grid-column: 1 / -1;
  grid-row: 3/ 4;
  color: $basic_white;
  text-align: center;
  font-size: 16px;
  line-height: 20px;

  @media (min-width: $tablet-width) and (max-width: $desktop-width) {
    width: 248px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 244px;
  }
}

@media (min-width: $tablet-width) and (max-width: $desktop-width){
  .add .button--product {
    width: 100%;
  }
}

a.button--view-all {
  background-color: $basic_grey ;
  width: 100%;
  padding-top: 10px;
  font-size: 16px;
  color: $special_dark-grey;

  @media (min-width: $tablet-width) {
    width: 249px;
    margin: 0 auto;
  }
}
